import { useState } from 'react';
import { Paper, Modal, InfoForm, Button } from 'components';
import { BsThreeDotsVertical } from 'react-icons/bs';
import { ReactComponent as Edit } from '../../assets/images/edit.svg';
import { ReactComponent as Delete } from '../../assets/images/delete.svg';
import {
  Item,
  BtnMenu,
  ContainerDropdown,
  InsideBtn,
} from './GeneraiCard.styled';

const GeneralCardItem = ({
  text,
  id,
  onDeleteCard,
  relation,
  toggleModal,
  isOpenModal,
  onEditCart,
}) => {
  const [showDropDown, setShowDropDown] = useState(false);

  const toggleDropDown = () => {
    setShowDropDown(!showDropDown);
  };

  return (
    <Paper>
      <Item>
        <span>{text}</span>
        <BtnMenu onClick={toggleDropDown}>
          <BsThreeDotsVertical />
        </BtnMenu>

        {showDropDown && (
          <ContainerDropdown>
            <InsideBtn type="button" onClick={() => toggleModal('edit')}>
              <Edit />
              
            </InsideBtn>

            {isOpenModal === 'edit' && (
              <Modal
                toggleModal={toggleModal}
                title={`   ${
                  relation === 'cities' ? ' ' : ' '
                }`}
                children={
                  <InfoForm
                    onSubmit={onEditCart}
                    relation={relation}
                    idItem={id}
                    title={relation === 'cities' ? '' : ''}
                  />
                }
              />
            )}

            <InsideBtn onClick={() => toggleModal('delete')} type="button">
              <Delete />
              
            </InsideBtn>
            {isOpenModal === 'delete' && (
              <Modal
                toggleModal={toggleModal}
                title={` ${
                  relation === 'cities' ? '' : ''
                }`}
                children={`       ${
                  relation === 'cities' ? ' ' : ' '
                }`}
                actions={
                  <>
                    <Button action={toggleModal} text={''} />
                    <Button
                      action={() => onDeleteCard(id, relation)}
                      text={''}
                    />
                  </>
                }
              />
            )}
          </ContainerDropdown>
        )}
      </Item>
    </Paper>
  );
};

export default GeneralCardItem;
